.avatar_container {
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    > img {
        border-radius: 50%;
    }
}

.avatar_msgContent {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    color: $background;
    background: $primary_variant;
    font: $fs_r_14;
    display: inline-flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}

