/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactForm_wrap {
    width: 50%;
    height: 100%;
    background-color: $bg;
    padding: 3rem 3rem 1.6rem 3rem;
    box-sizing: border-box;
}

.contactForm_title {
    font: $fs_r_16;
    color: $gray_1;
    line-height: 2.4rem;
    margin-bottom: 1.5rem;
}

.contactForm_saveBtn {
    @include body_2;
    background-color: $primary_600;
    box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
    &[disabled] {
        background-color: $neutral_300 !important;
    }
}

.contactForm_btn {
    margin-top: 3.2rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.contactForm_cancelBtn {
    @include body_2;
    margin-right: 1rem;
    background-color: transparent !important;
    border: none !important;
    color: $primary_600 !important;
}

// cn style
.contactForm_cn {
    .contactForm_title {
        @include c_h_5;
    }
}
