/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin transition {
    transition: all 0.1s ease;
}
@mixin checkBox($width, $height) {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: $width;
    height: $height;
    cursor: pointer;
    overflow: hidden;
    flex: 0 0 auto;
    box-sizing: border-box;
}
@mixin checkContent {
    font-size: 1.4rem;
    line-height: 2rem;
    font-weight: 400;
    cursor: pointer;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */
.check_wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    height: 3.2rem;
    margin-bottom: 1.6rem;
    &:last-of-type {
        margin-bottom: 0;
    }
}

.check_wrap__solid {
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0.7rem 0.8rem;
    border: 0.06rem solid $neutral_200;
    border-radius: 0.6rem;
}

.check_icon {
    @include checkBox(1.6rem, 1.6rem);
    margin-right: 1rem;
}

.check_icon_solid {
    @include checkBox(1.4rem, 1.4rem);
    border: 0.06rem solid $neutral_200;
    border-radius: 0.3rem;
    margin-right: 1.2rem;
}

.check_icon_solid__default {
    width: 0.8rem;
    height: 0.8rem;
    background-color: transparent;
}

.check_icon_solid__active {
    width: 0.8rem;
    height: 0.8rem;
    border-radius: 0.2rem;
    background-color: $primary_600;
    box-sizing: border-box;
}

.check_defaultIcon {
    border: 1px solid $line_light;
    box-sizing: border-box;
    border-radius: 2px;
    width: 1.6rem;
    height: 1.6rem;
    @include transition();
}

.check_defaultIconHover {
    border-color: $primary_400;
}

.check_checkIcon {
    color: $primary_600;
    box-sizing: border-box;
    transform-origin: center;
    flex: 0 0 auto;
    font-size: 1.6rem;
}

.check_isClick {
    animation: 0.2s ease checkZoomFade;
}

.check_iconDisabled {
    .check_defaultIcon,
    .check_icon_solid {
        background-color: $neutral_100;
    }
    .check_checkIcon,
    .check_content,
    .check_content_solid {
        color: $neutral_300;
    }
    .check_icon,
    .check_content,
    .check_icon_solid,
    .check_content_solid {
        cursor: not-allowed;
    }
}

.check_content {
    flex: 0 0 auto;
    order: 1;
    cursor: pointer;
}

.check_content_solid {
    @include checkContent();
    color: $neutral_500;
}

.check_content_solid__active {
    @include checkContent();
    color: $primary_600;
}

@keyframes checkZoomFade {
    from {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
