/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */
.MergeInput_container {
    border-radius: 0.4rem;
    border: 1px solid $line_light;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    &:hover {
        border-color: rgba($color: $primary_variant, $alpha: 0.5);
    }
}

.MergeInput_container__disabled:hover {
    border-color: $line_light;
}

.MergeInput_selectContainer {
    flex: 0 0 auto;
    position: relative;
    width: calc(8.7rem - 1.5rem);
    padding-right: 1.5rem;
    height: 100%;
}

.MergeInput_select {
    color: $primary_variant;
    font: $fs_l_12;
    width: calc(100% - 1.5rem);
    height: 100%;
    padding: 0 0 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.MergeInput_text {
    width: 100%;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.MergeInput_dataList {
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.MergeInput_dataItem {
    text-align: left;
    padding: 0.5rem 1.5rem;
    cursor: pointer;
    &:hover {
        background-color: $hover;
    }
}

.MergeInput_dragIcon {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    font-size: 1.45rem;
    pointer-events: none;
    transform: rotate(0);
    transition: transform 0.5s ease;
    color: $line_dark;
    font-size: 0.8rem;
}

.MergeInput_line {
    display: block;
    width: 1px;
    height: 1.8rem;
    background-color: $not_activated;
    margin: 0 1.5rem;
    flex: 0 0 auto;
}

.MergeInput_input {
    flex: 0 0 auto;
    width: calc(100% - 1px - 8.7rem + 1.5rem - 3rem - 3rem);
    border: 0;
    outline: none;
    background-color: transparent;
    font: $fs_l_12;
    padding-right: 1.5rem;
}

.MergeInput_dataItem__active {
    color: $primary_variant;
}
/* <--------------------------- * SECTION1 * --------------------------- */
