/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../../../Constant/font.scss" as *;
@use "../../../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactForm_wrap {
    width: 50%;
    height: 100%;
    background-color: rgba($gray_6, 0.5);
    padding: 4rem 3rem 0 3rem;
    box-sizing: border-box;
}

.contactForm_title {
    font: $fs_r_16;
    color: $gray_1;
    line-height: 2.4rem;
    margin-bottom: 1.5rem;
}

.contactForm_saveBtn {
    margin-right: 1.2rem;
    &[disabled] {
        background-color: $gray_4 !important;
    }
}

.contactForm_btn {
    margin-top: 3.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactForm_cancelBtn {
    &[disabled] {
        background-color: transparent !important;
        color: $gray_4 !important;
        border: 1px solid $gray_4 !important;
        font: $fs_m_14;
        line-height: 2.2rem;
    }
}
