/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import "../../../../../Constant/breakPoint.scss";
@import "../../../../../Constant/font.scss";
@import "../../../../../Constant/color.scss";
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactPhone_wrapper {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 3rem;
    > div:nth-child(2) {
        background-color: $background;
    }
}

.contactPhone_name {
    font: $fs_r_12;
    color: $neutral_600;
    line-height: 2rem;
    margin-bottom: 0.5rem;
}

.contactPhone_container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    width: 27.5rem;
    height: 4rem;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid $line_light;
    transition: all 0.5s ease;

    &:hover:not(.contactPhone_container__disabled) {
        border-color: rgba($primary_variant, 0.5);
    }

    &.contactPhone_container__active:not(.contactPhone_container__disabled) {
        border-color: $primary_variant;
    }

    &.contactPhone_container__error {
        border-color: $danger_600 !important;
    }

    &.contactPhone_container__disabled {
        .contactPhone_areaCodeData {
            cursor: auto;
        }
    }
}

.contactPhone_ipt_errormsg {
    position: absolute;
    bottom: -2rem;
    left: 0;
    color: $danger_600;
}

.contactPhone_areaCodeContainer {
    width: 9.2rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    height: 100%;
    z-index: 0;
    &::after {
        content: "";
        height: 2.2rem;
        width: 1px;
        top: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: 10;
        background-color: $line_light;
        margin: auto 0;
    }
}

.contactPhone_areaCodeList {
    position: absolute;
    left: 0;
    bottom: calc(100% + 1px);
    background-color: #fdfdfd;
    box-shadow: 0px -2px 5px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    max-height: 25rem;
    height: auto !important;
    z-index: -9;
    transform: scaleY(0);
    opacity: 0;
    transform-origin: bottom center;
    transition: all 0.3s ease;
    div[class*="scroll_scrollBody"] {
        max-height: 25rem;
    }
    &.contactPhone_areaCodeList__active {
        transform: scaleY(1);
        opacity: 1;
        z-index: 1;
    }
}

.contactPhone_cityItem {
    font: $fs_r_14;
    height: 2.4rem;
    color: $gray_2;
    padding-left: 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 1rem;
    &:last-of-type {
        margin-bottom: 0;
    }
    &:hover {
        background-color: rgba($hover, 0.3);
    }
}

.contactPhone_cityItemImg {
    width: 2rem;
    margin-right: 0.5rem;
}

.contactPhone_areaCodeData {
    cursor: pointer;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 2.5rem;
    box-sizing: border-box;
}

.contactPhone_areaCodeDataContent {
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font: $fs_r_14;
    color: $gray_2;
    line-height: 2.2rem;
}

.contactPhone_areaCodeDataContentImg {
    width: 2rem;
    margin-right: 0.5rem;
}

.contactPhone_dropDownIcon {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    font-size: 1rem;
    color: $line_light;
    right: 0.8rem;
    pointer-events: none;
    transition: all 0.3s ease;
    &.contactPhone_dropDownIcon__active {
        transform: rotate(180deg);
    }
}

.contactPhone_phoneIptContent {
    width: 100%;
    outline: none;
    background-color: transparent;
    border: 0;
    box-sizing: border-box;
    padding: 0 3rem 0 1rem;
    font: $fs_r_14;
    color: $gray_2;
    line-height: 2.2rem;
}

.contactPhone_phoneIptContainer {
    position: relative;
    z-index: 0;
    flex: 1 1 calc(100% - 9.2rem);
}

.contactPhone_reset {
    cursor: pointer;
    right: 1rem;
    font-size: 1.8rem;
    color: $line_light;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
}

// cn style
.contactPhone_cn {
    .contactPhone_name {
        @include c_body_3;
    }
    input[class*="mobileInputV2_ipt"] {
        @include c_body_3;
        &::placeholder {
            color: $neutral_300;
        }
    }
}
