/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.steps_container {
    background: $background;
    display: inline-flex;
    align-items: flex-start;
    justify-content: center;
}

.steps_itemContainer {
    display: inline-block;
    height: 9rem;
    position: relative;
}

@mixin circle {
    height: 0.8rem;
    width: 0.8rem;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 2.8rem;
}

.steps_circle {
    @include circle();
    background: $line_light;
    cursor: pointer;
}

.steps_itemContainer__line {
    position: absolute;
    content: "";
    height: 0.2rem;
    width: 16.5rem;
    top: 3.1rem;
    background: $hover;
}

@mixin label {
    height: 2.4rem;
    padding-top: 1rem;
    width: 8rem;
    user-select: none;
    cursor: pointer;
    position: relative;
}

.steps_label__reached {
    @include label();
    .steps_text {
        color: $type;
    }
}

.steps_label__unreached {
    @include label();
    .steps_text {
        color: $line_dark;
    }
}

.steps_text {
    position: absolute;
    font: $fs_r_14;
    white-space: nowrap;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
}

/* <--------------------------- * SECTION1 * --------------------------- */
