/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.mobileInput_container {
    border: 0.1rem solid $line_light;
    border-radius: 0.4rem;
    user-select: none;
    font: $fs_r_14;
    &:hover {
        border-color: rgba($color: $primary_variant, $alpha: 0.5);
    }
}
.mobileInput_container__disabled:hover {
    border-color: $line_light;
}

.mobileInput_country {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 1rem;
    box-sizing: border-box;
    position: relative;
    padding-right: 3rem;
    vertical-align: top;
    cursor: pointer;
}

.mobileInput_country:focus {
    outline: none;
}

.mobileInput_down {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0rem;
    width: 2rem;
    border-right: 0.1rem solid $line_light;
    font-size: 1.8rem;
    color: $line_light;
}

.mobileInput_countryValue {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.mobileInput_downContainer {
    position: absolute;
    color: $type;
    left: 0rem;
    border-radius: 0.4rem;
    border: 0.1rem solid $line_light;
    overflow-x: hidden;
    overflow-y: scroll;
    box-sizing: border-box;
    background: $background;
}

.mobileInput_downItem {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-left: 1rem;
    transition: 0.1s;
}

.mobileInput_downItem:hover {
    color: $primary_variant;
    transition: 0.1s;
}

.mobileInput_input {
    display: inline-block;
    border: none;
    padding: 0 1rem;
    box-sizing: border-box;
    font: $fs_r_14;
    color: $type;
    appearance: none;
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        appearance: none !important;
        margin: 0;
    }
}

.mobileInput_input:focus {
    outline: none;
}

.mobileInput_input::placeholder {
    color: $not_activated;
}

.mobileInput_downIcon {
    color: $line_light;
    font-size: 0.8rem;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    transition: all 0.2s ease;
    right: 0.5rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
