/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
.previewImage_wrap {
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    border: 1px solid $not_activated;
    display: inline-block;
    box-sizing: border-box;
    &:not(.previewImage_wrap__loading):hover {
        .previewImage_previewBtn {
            opacity: 1;
            pointer-events: initial;
        }
        .previewImage_removeBtn {
            opacity: 1;
            pointer-events: initial;
        }
    }
}

.previewImage_img {
    width: 6rem;
    height: 5rem;
    object-fit: contain;
    object-position: 50% 50%;
}

.previewImage_previewBtn {
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    position: absolute;
    background-color: rgba($type, 0.5);
    height: 100%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s linear;
    pointer-events: none;
}

.previewImage_previewIcon {
    font-size: 1.2rem;
    color: $background;
}

.previewImage_removeBtn {
    position: absolute;
    width: 0;
    height: 0;
    border-width: 1rem;
    border-color: #ff525d #ff525d transparent transparent;
    border-style: solid;
    top: 0;
    right: 0;
    opacity: 0;
    transition: all 0.2s linear;
    pointer-events: none;
    cursor: pointer;
    &:hover {
        .previewImage_removeIcon {
            transform: scale(1.5);
        }
    }
}

.previewImage_removeIcon {
    color: $background;
    transition: all 0.2s linear;
    font-size: 0.6rem;
    position: absolute;
    top: -0.7rem;
    right: -0.7rem;
}

.previewImage_loading {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba($type, 0.5);
}
