/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.iconDropDownList_outerContainer {
    position: relative;
    user-select: none;
}

.iconDropDownList_container {
    font: $fs_m_14;
    display: inline-flex;
    background: $primary_variant;
    border-radius: 0.4rem;
    box-sizing: border-box;
    justify-content: space-between;
    cursor: pointer;
    outline: none;
}

.iconDropDownList_selectedItem {
    display: inline-block;
    margin-left: 0.6rem;
    margin-right: 0.6rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: $background;
    text-align: center;
    width: calc(100% - 3.8rem);
}

.iconDropDownList_icon {
    display: inline-block;
    margin-left: 1.2rem;
    color: $background;
    transition: 0.1s linear;
    font-size: 1.4rem;
}

.iconDropDownList_openButton {
    margin-right: 1.2rem;
    transition: 0.2s linear;
    color: $background;
    font-size: 1.2rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.iconDropDownList_itemContentOverFlow {
    font: $fs_r_14;
    box-shadow: $shadow_01dp;
    border-radius: 0.5rem;
    background: $background;
}

.iconDropDownList_itemContent {
    padding-top: 1.5rem;
    background: $background;
    overflow: auto;
    max-height: 30rem;
}

.iconDropDownList_itemStatus {
    display: inline-block;
    height: 2.2rem;
    width: 4.5rem;
    text-align: center;
    line-height: 2.2rem;
    vertical-align: top;
}

.iconDropDownList_item {
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.iconDropDownList_itemTag {
    display: inline-block;
    height: 2.2rem;
    line-height: 2.2rem;
    margin-bottom: 1.5rem;
    cursor: pointer;
    vertical-align: top;
    padding-right: 1rem;
    user-select: none;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.iconDropDownList_item:hover > .iconDropDownList_itemTag {
    color: rgba($color: $primary_variant, $alpha: 1);
    transition: 0.1s linear;
}

.iconDropDownList_placeholder {
    color: #bdbdbd;
}

/* <--------------------------- * SECTION1 * --------------------------- */
