/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin container {
    z-index: 150 !important;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

.popover_vertical {
    @include container();
}

.popover_horizontal {
    @include container();
}

.popover_body {
    width: 10.8rem;
    height: 7.5rem;
    border-radius: 4px;
    background-color: rgba($type, 0.7);
    box-shadow: $shadow_06dp;
    color: $background;
    font: $fs_r_12;
    line-height: 2rem;
    padding: 0.7rem 1rem;
}
