/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/

@mixin iconBtn {
    width: 2.2rem;
    height: 2.2rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    user-select: none;
    transition: box-shadow 0.2s;
    border-radius: 0.2rem;
    &:not([class*="__gray"]):hover {
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    }
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */

.paginationV2_container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row nowrap;
}

.paginationV2_preColumnBtn {
    @include iconBtn();
    margin-right: 1rem;
    &.paginationV2_preColumnBtn__gray {
        cursor: context-menu;
        .paginationV2_icon {
            color: $gray_4;
        }
    }
    .paginationV2_icon {
        font-size: 1.4rem;
    }
}

.paginationV2_preBtn {
    @include iconBtn();

    margin-right: 1rem;
    &.paginationV2_preBtn__gray {
        cursor: context-menu;
        .paginationV2_icon {
            color: $gray_4;
        }
    }
    .paginationV2_icon {
        font-size: 1.15rem;
        transform: rotate(180deg);
    }
}

.paginationV2_nextBtn {
    @include iconBtn();
    margin-left: 1rem;

    &.paginationV2_nextBtn__gray {
        cursor: context-menu;
        .paginationV2_icon {
            color: $gray_4;
        }
    }
    .paginationV2_icon {
        font-size: 1.15rem;
    }
}

.paginationV2_nextColumnBtn {
    @include iconBtn();
    margin-left: 1rem;
    &.paginationV2_nextColumnBtn__gray {
        cursor: context-menu;
        .paginationV2_icon {
            color: $gray_4;
        }
    }
    .paginationV2_icon {
        font-size: 1.4rem;
    }
}

.paginationV2_listContainer {
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    margin: 0;
    padding: 0;
}

.paginationV2_itemContainer {
    color: $gray_2;
    font: $fs_r_14;
    height: 2.2rem;
    padding: 0 0.5rem;
    min-width: 2.2rem;
    border: 1px solid transparent;
    margin: 0 0.5rem;
    box-sizing: border-box;
    border-radius: 6px;
    list-style: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    transition: box-shadow 0.2s;

    &:hover {
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    }
}

.paginationV2_itemActive {
    border-color: $primary_variant;
    color: $primary_variant;
}

.paginationV2_icon {
    color: $primary_variant;
}
