.contacts_v2_listSearch {
    display: flex;
    width: 28.3rem;
    height: 3.6rem;
    border: 0.08rem solid $neutral_200;
    border-radius: 5rem;
    padding: 0 1.6rem;
    margin: 0 auto 2.4rem;

    &_input {
        width: 90%;
        @include c_body_3;
    }

    &_input::placeholder {
        color: $neutral_300;
    }

    &_icon {
        font-size: 1.6rem;
        font-weight: bold;
        color: $primary_600;
        cursor: pointer;
    }

    &:focus-within {
        border-color: $primary_700;
        box-shadow: none;
    }
}

.contacts_v2_listSearch_wrap {
    width: 28.3rem;
    padding: 1.2rem 0.4rem;
    background-color: $background;
    box-sizing: border-box;
    border-radius: 0.6rem;
    box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
}

.contacts_v2_listSearch_item {
    width: 100%;
    display: inline-flex;
    align-items: center;
    box-sizing: border-box;
    height: 3.2rem;
    padding: 0.4rem 0.8rem;
    border-radius: 0.6rem;
    cursor: pointer;
    &:hover {
        background-color: $primary_10;
    }
    span {
        width: 100%;
        font-size: 1.4rem;
        line-height: 2rem;
        color: rgba($color: $neutral_600, $alpha: 0.5);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        span {
            font-weight: bold;
            color: $neutral_600;
        }
    }
}
