.labels_container {
    display: inline-block;
    text-align: center;
    user-select: none;
    font: $fs_r_10;
    color: $background;
    border-radius: 1.4rem;
    border: 0.1rem solid transparent;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.labels_container__transparent {
    border: 0.1rem solid $primary_variant;
    color: $primary;
}

.labels_container__green {
    background: $success_600;
}

.labels_container__orange {
    background: $secondary_variant;
}

.labels_container__blue {
    background: $info_600;
}

.labels_container__red {
    background: #eb5757;
}

.labels_container__yellow {
    background: #f2c94c;
}
