/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.mobileInputV2_wrapper {
    width: 100%;
    height: 4rem;
    border: 1px solid $neutral_200;
    box-sizing: border-box;
    border-radius: 0.6rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    overflow: hidden;
    transition: all 0.5s ease;

    &:hover:not(.mobileInputV2_wrapper__disabled) {
        border-color: $primary_500;
    }

    &.mobileInputV2_wrapper__active:not(.mobileInputV2_wrapper__disabled) {
        border-color: $primary_variant;
    }

    &.mobileInputV2_wrapper__error {
        border-color: $danger_600 !important;
    }

    &.mobileInputV2_wrapper__disabled {
        .mobileInputV2_areaContainer {
            cursor: auto;
        }
    }
}

.mobileInputV2_areaContainer {
    width: 9.2rem;
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    padding-right: 0.3rem;
    box-sizing: border-box;
    outline: none;
    cursor: pointer;
}

.mobileInputV2_areaCodeContent {
    padding-left: 1rem;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    font: $fs_r_14;
    color: $gray_2;
    width: calc(100% - 2.2rem);
    box-sizing: border-box;
    user-select: none;
}

.mobileInputV2_areaImg {
    width: 2rem;
    margin-right: 0.5rem;
}

.mobileInputV2_dropDownIconContainer {
    width: 2.2rem;
    height: 1.8rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.mobileInputV2_dropDownIcon {
    color: $neutral_200;
    font-size: 0.8rem;
}

.mobileInputV2_splitLine {
    width: 1px;
    height: 2.2rem;
    background-color: $neutral_200;
}

.mobileInputV2_phoneNumber {
    width: calc(100% - 9.2rem - 1px);
    position: relative;
    height: 100%;
}

.mobileInputV2_ipt {
    border: 0;
    outline: none;
    width: 100%;
    height: 100%;
    padding-left: 1rem;
    padding-right: 3rem;
    font: $fs_r_14;
    color: $gray_2;
    box-sizing: border-box;
    background-color: transparent;
}

.mobileInputV2_reset {
    cursor: pointer;
    right: 1rem;
    font-size: 1.8rem;
    color: $neutral_200;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
}

.mobileInputV2_areaCodeList {
    background-color: #fdfdfd;
    box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0 0 4px 4px;
    div[class*="scroll_scrollBody"] {
        max-height: 25rem;
    }
}

.mobileInputV2_areaCodeItem {
    font: $fs_r_14;
    height: 2.4rem;
    color: $gray_2;
    padding: 0.7rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    &:last-of-type {
        margin-bottom: 0;
    }
    &:hover {
        background-color: rgba($hover, 0.3);
    }
}

.mobileInputV2_areaCodeItemImg {
    width: 2rem;
    margin-right: 0.5rem;
}
