/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.dropDownList_outerContainer {
    user-select: none;
}

.dropDownList_container {
    font: $fs_r_14;
    display: inline-flex;
    border: 0.1rem solid $line_light;
    border-radius: 0.4rem;
    box-sizing: border-box;
    justify-content: space-between;
    position: relative;
    cursor: pointer;
    outline: none;
    background: $background;
    &.dropDownList_outerContainer__active {
        border: 1px solid rgb(71, 141, 165);
    }
}

.dropDownList_container__noBorder {
    font: $fs_r_14;
    display: inline-flex;
    box-sizing: border-box;
    justify-content: space-between;
    cursor: pointer;
    position: relative;
    outline: none;
    background: $background;
    &.dropDownList_outerContainer__active {
        border: 1px solid rgb(71, 141, 165);
    }
}

.dropDownList_selectedItem {
    display: inline-block;
    margin-left: 1.2rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: $type;
}

.dropDownList_openButton {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-flow: row nowrap;
    margin-right: 1.2rem;
    color: $line_light;
    transition: 0.1s linear;
}

.dropDownList_openButtonIcon {
    font-size: 1.2rem;
}

.dropDownList_itemContentOverFlow {
    font: $fs_r_14;
    box-shadow: $shadow_01dp;
    border-radius: 0.5rem;
    overflow: auto;
    background: $background;
}

.dropDownList_itemContent {
    padding: 0.5rem 0;
    background: $background;
    color: $type;
}

.dropDownList_itemStatus {
    display: inline-block;
    height: 2.2rem;
    width: 4.5rem;
    text-align: center;
    line-height: 2.2rem;
    vertical-align: top;
}

.dropDownList_item {
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.dropDownList_itemTag {
    display: inline-flex;
    height: 4rem;
    line-height: 4rem;
    width: 100%;
    padding-left: 1.5rem;
    cursor: pointer;
    align-items: center;
    padding-right: 1rem;
    user-select: none;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.dropDownList_item:hover > .dropDownList_itemTag {
    background: rgba(71, 141, 165, 0.15);
}

.dropDownList_itemRemove {
    color: $error !important;
    border-top: 0.1rem solid $line_light;
    &:hover > .dropDownList_itemTag {
        color: $error !important;
    }
}

.dropDownList_itemActive {
    color: $primary_variant;
}

.dropDownList_placeholder {
    color: #bdbdbd;
}

/* <--------------------------- * SECTION1 * --------------------------- */
