/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/breakPoint.scss" as *;
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
@mixin fixedBtn {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4.8rem;
    height: 4.8rem;
    border-radius: 50%;
    background-color: rgba($black, 0.2);
    cursor: pointer;
    font-size: 2rem;
    color: $white;
    transition: all 0.3s;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.dr_image {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
}

.dr_image_img {
    width: 100%;
    height: auto;
    box-sizing: border-box;
}

.dr_image__placeholder {
    position: absolute;
    inset: 0;
    box-sizing: border-box;
}

.dr_image_img__placeholder {
    background-color: rgba(0, 0, 0, 0.04);
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 30%;
}

.dr_image_mask {
    position: absolute;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    color: $white;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s;
    &:hover {
        opacity: 1;
    }
}

.dr_image_mask_info {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding: 0 0.4rem;

    & > svg {
        margin-right: 0.4rem;
    }
}

// preview operation
.preview_close {
    @include fixedBtn;
    right: 6rem;
    top: 6rem;
}

.preview_operations_progress {
    @include body_2;
    position: fixed;
    display: inline-block;
    top: 6rem;
    left: 50%;
    transform: translateX(-50%);
    padding: 0.4rem 1.2rem;
    border-radius: 0.4rem;
    background-color: rgba($black, 0.4);
    color: $white;
}

.preview_switch_left,
.preview_switch_right {
    @include fixedBtn;
    top: 50%;
    transform: translateY(-50%);
}

.preview_switch_left {
    left: 3rem;
}

.preview_switch_right {
    right: 3rem;
}

.preview_switch_left__disabled,
.preview_switch_right__disabled {
    cursor: not-allowed;
    background-color: transparent;
    color: $neutral_100;
}

.preview_operations {
    list-style: none;
    margin: 0;
    padding: 1rem 1.6rem;
    display: inline-flex;
    position: fixed;
    left: 50%;
    bottom: 7rem;
    transform: translateX(-50%);
    align-items: center;
    background-color: rgba($neutral_200, 0.8);
    border-radius: 0.6rem;
    pointer-events: auto;
}

.preview_operations_operation {
    display: inline-flex;
    align-items: center;
    font-size: 2rem;
    margin-right: 3.2rem;
    padding: 0.4rem;
    color: $neutral_600;
    cursor: pointer;
    &:last-child {
        margin-right: 0;
    }
    &:hover {
        background-color: $neutral_100;
        border-radius: 0.4rem;
    }
    svg {
        vertical-align: inherit;
    }
}

.preview_operations_operation.preview_operations_operation__disabled {
    color: $neutral_400;
    background-color: transparent;
    cursor: not-allowed;
}

.preview_operations_operation_minification {
    margin-right: 2rem;
}

.preview_operations_scalePercent {
    @include body_2;
    margin-right: 2rem;
    user-select: none;
}

.preview_operations_operation_tooltip {
    width: auto;
    height: auto;
    white-space: nowrap;
}

// image
.preview_wrap {
    width: 100%;
    height: 100%;
    text-align: center;
    overflow: hidden;
    z-index: 99;
}

.preview_image_wrapper {
    position: absolute;
    inset: 0;
    transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
    &::before {
        content: "";
        display: inline-block;
        width: 0.1rem;
        height: 50%;
        margin-inline-end: -0.1rem;
    }
}

.preview_image {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
    cursor: grab;
    user-select: none;
    pointer-events: auto;
    transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
}

// preview mask
.preview_wrap_form_enter,
.preview_wrap_to_leave {
    opacity: 0;
    scale: 0;
}

.preview_wrap_form_leave,
.preview_wrap_to_enter {
    opacity: 1;
    scale: 1;
}

.preview_wrap_form_active,
.preview_wrap_to_active {
    transition: all 0.4s cubic-bezier(0.57, 0.01, 0.02, 0.99), box-shadow 0s;
}

.preview_mask_form_enter,
.preview_mask_to_leave {
    opacity: 0;
}

.preview_mask_form_to,
.preview_mask_to_leave {
    opacity: 0.3;
}

.preview_mask_form_active,
.preview_mask_to_leave {
    transition: all 0.4s linear;
}

.preview_mask {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: $neutral_600;
    opacity: 0.3;
    z-index: 98;
}

// cn
[class*="_cn"] {
    @include c_body_3;
}
/* <--------------------------- * SECTION1 * --------------------------- */
