/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin transition {
    transition: all 0.2s ease;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */
.radio_wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    height: 3.2rem;
    margin-bottom: 1.6rem;
    &:last-of-type {
        margin-bottom: 0;
    }
}

.radio_icon {
    width: 1.6rem;
    height: 1.6rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-flow: row nowrap;
    margin-right: 1rem;
    cursor: pointer;
    flex: 0 0 auto;
}

.radio_defaultIcon {
    border: 1px solid $neutral_300;
    box-sizing: border-box;
    border-radius: 50%;
    width: 1.6rem;
    height: 1.6rem;
    @include transition();
}

.radio_defaultIconHover {
    border-color: $primary_400;
}

.radio_checkIcon {
    border: 1px solid $primary_600;
    box-sizing: border-box;
    border-radius: 50%;
    width: 1.6rem;
    height: 1.6rem;
    @include transition();
    transform-origin: center center;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        top: calc(50% - 0.4rem);
        left: calc(50% - 0.4rem);
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 50%;
        background-color: $primary_600;
        transform-origin: center center;
    }
    &.radio_isClick::after {
        animation: 0.2s ease radioZoomFade;
    }

    &.radio_defaultIconHover {
        &::before {
            content: "";
            transform-origin: center center;
            width: 2.2rem;
            height: 2.2rem;
            border-radius: 50%;
            border: 3px solid $primary_10;
            position: absolute;
            top: calc(50% - 1.1rem);
            box-sizing: border-box;
            left: calc(50% - 1.1rem);
        }
        &.radio_isClick::before {
            animation: 0.2s radioFade;
        }
    }
}

@keyframes radioZoomFade {
    from {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes radioFade {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.radio_iconDisabled {
    .radio_defaultIcon {
        background-color: $neutral_100;
    }
    .radio_checkIcon {
        border-color: $neutral_300;
        &::after {
            background-color: $neutral_300;
        }
    }
    .radio_icon,
    .radio_content {
        cursor: auto;
    }
}

.radio_content {
    flex: 0 0 auto;
    cursor: pointer;
}
