/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin centerIcon {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    transition: all 0.5s ease;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */

.contacts_wrap {
    display: inline-table;
}

.contacts_openIcon {
    font-size: 1.5rem;
    color: $line_dark;
    transition: all 0.5s ease;
    user-select: none;
}

.contacts_toggleBtn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:hover {
        .contacts_openIcon {
            color: rgba($primary_variant, 0.5);
        }
    }
}

.contacts_toggleBtn__active.contacts_toggleBtn {
    .contacts_openIcon {
        color: $primary_variant;
    }
}

.contacts_floatingWrap {
    width: 65rem;
    height: 41.5rem;
    background-color: $background;
    border-radius: 5px;
    box-shadow: $shadow_04dp;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-flow: row nowrap;
}

.contacts_listContainer {
    width: 50%;
    height: 100%;
}

.contacts_listTop {
    height: 9.6rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contacts_listTopName {
    position: relative;
    font: $fs_r_24;
    color: $type;
}

.contacts_listTopHr {
    width: 4rem;
    height: 0.4rem;
    background-color: $primary_variant;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 1rem);
    margin: 0 auto;
}

.contacts_listAdd {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 5.9rem;
}

.contacts_listAddBtn {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font: $fs_r_14;
    color: $primary_variant;
    transition: all 0.5s ease;
}

.contacts_listAddIcon {
    font-size: 1.4rem;
    color: $primary_variant;
    margin-right: 0.65rem;
    transform: all 0.5s ease;
}

.contacts_closeIcon {
    position: absolute;
    right: 2rem;
    top: 2rem;
    cursor: pointer;
    color: $line_light;
    transition: all 0.3s ease;
    &:hover {
        transform: scale(1.3);
    }
}

.contacts_listAddBtn.contacts_listAddBtn_gray {
    cursor: context-menu;
    color: $not_activated;
    .contacts_listAddIcon {
        color: $not_activated;
    }
}

.contacts_listContent {
    margin: 0;
    padding: 0;
}

.contacts_contactItem {
    list-style: none;
    margin: 0;
    width: 100%;
    height: 6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    &:not(.contacts_contactItem__disabled):hover {
        .contacts_contactPersonal {
            opacity: 0;
            z-index: -1;
        }
        .contacts_removeIcon {
            z-index: 1;
            opacity: 1;
        }
    }
}

.contacts_contactIcon {
    width: 6rem;
    height: 100%;
    position: relative;
}

.contacts_contactPersonal {
    width: 2rem;
    height: 2rem;
    background-color: $line_light;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    @include centerIcon();
    z-index: 1;
    border-radius: 50%;
}

.contacts_contactPersonalIcon {
    font-size: 1.2rem;
    color: $background;
}

.contacts_removeIcon {
    color: #eb5757;
    font-size: 2rem;
    z-index: -1;
    opacity: 0;
    @include centerIcon();
    cursor: pointer;
}

.contacts_contactData {
    display: flex;
    width: calc(100% - 6rem);
    height: 100%;
    align-items: flex-start;
    justify-content: center;
    flex-flow: column nowrap;
}

.contacts_contactName {
    font: $fs_r_14;
    color: $gray_1;
    line-height: 2.2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.contacts_contactEmail {
    font: $fs_r_12;
    color: $line_dark;
    line-height: 2rem;
}

.contacts_contactItem__active {
    background-color: rgba($gray_6, 0.5);
}
