/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../../../Constant/breakPoint.scss" as *;
@use "../../../../../Constant/font.scss" as *;
@use "../../../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
@mixin content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: row nowrap;
    flex: 0 0 auto;
    letter-spacing: 0.25px;
    font: $fs_r_14;
    line-height: 2rem;
    color: $neutral_600;
    box-sizing: border-box;
}

.navigationLink_item {
    flex: 0 0 auto;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    position: relative;
    margin-right: 6rem;
    height: 100%;
    box-sizing: border-box;
}

.navigationLink_dropdownIcon {
    font-size: 0.9rem;
    color: $neutral_600;
    margin-left: 0.9rem;
    flex: 0 0 auto;
    box-sizing: border-box;
}

.navigationLink_itemContent {
    @include content();
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0 auto;
    cursor: pointer;
}

.navigationLink_itemActive {
    position: relative;

    .navigationLink_itemContent {
        font-weight: bold;
        font-size: 1.6rem;
        letter-spacing: 0.15px;
        color: $primary_700;
        transition: all 0.2s linear;
    }
    &::after {
        content: "";
        width: 1.4rem;
        height: 0.3rem;
        border-radius: 5px;
        background-color: $primary_700;
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        bottom: 1.4rem;
    }
}

.navigationLink_itemContentPlaceholder {
    @include content();
    pointer-events: none;
    opacity: 0;
    font-weight: bold;
    font-size: 1.6rem;
    letter-spacing: 0.15px;
    box-sizing: border-box;
}

.navigationLink_itemHover {
    .navigationLink_itemContent {
        font-weight: bold;
        font-size: 1.6rem;
        letter-spacing: 0.15px;
        color: $primary_700;
        transition: all 0.2s linear;

        .navigationLink_dropdownIcon {
            transform: rotate(180deg);
            transition: all 0.2s linear;
        }
    }
}

@media screen and (max-width: 1441px) and (min-width: $desktop) {
    .navigationLink_item {
        margin-right: 4rem;
    }
}
