.counterV2_container {
    border: 1px solid $not_activated;
    box-sizing: border-box;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.8rem 1.6rem;
}

.counterV2_container.counterV2_container_vertical {
    .counterV2_inputContainer {
        text-align: left;
    }
    .counterV2_operation_button {
        display: flex;
        flex-direction: column;
        .counterV2_subtractBtnIcon,
        .counterV2_addBtnIcon {
            font-size: 1rem;
        }
        .counterV2_subtractBtnIcon {
            transform: rotate(-90deg);
        }
        .counterV2_addBtnIcon {
            transform: rotate(90deg);
        }
    }
}

.counterV2_inputContainer {
    @include body_1;
    outline: none;
    color: $gray_2;
    border: 0;
    margin: 0;
    padding: 0;
    width: calc(100% - 3rem);
    text-align: center;
    display: inline-block;
}

.counterV2_inputContainer__disabled {
    color: $line_light;
    background: transparent;
    pointer-events: none;
}

.counterV2_addBtn,
.counterV2_subtractBtn {
    width: 1.3rem;
    height: 1.3rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    &__disabled {
        cursor: not-allowed;
    }
    > svg {
        font-size: 1.6rem;
    }
}

.counterV2_subtractBtnIcon,
.counterV2_addBtnIcon {
    color: $neutral_400;
}

.counterV2_subtractBtnIcon:hover,
.counterV2_addBtnIcon:hover {
    color: $primary_400;
}

.counterV2_addBtn__disabled .counterV2_addBtnIcon,
.counterV2_subtractBtn__disabled .counterV2_subtractBtnIcon {
    color: $neutral_300;
}
