/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import "../../../../../Constant/breakPoint.scss";
@import "../../../../../Constant/font.scss";
@import "../../../../../Constant/color.scss";
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactEmail_wrapper {
    margin-bottom: 3rem;
}

.contactEmail_name {
    font: $fs_r_12;
    color: $neutral_600;
    line-height: 2rem;
    margin-bottom: 0.5rem;
}

.contactEmail_ipt {
    position: relative;
    top: 0;
    left: 0;
    box-sizing: border-box;
}

.contactEmail_iptContent {
    box-sizing: border-box;
    background-color: $background;
    input[class*="resetInput_input"] {
        font: $fs_r_14;
        color: $gray_2;
        line-height: 100% !important;
    }
}

.contactEmail_ipt__errormsg {
    position: absolute;
    bottom: -2rem;
    left: 0;
    color: $danger_600;
}

.contactEmail_iptContent__error {
    border-color: $danger_600 !important;
}

// cn style
.contactEmail_cn {
    .contactEmail_name {
        @include c_body_3;
    }
    input[class*="resetInput_input"] {
        @include c_body_3;
        &::placeholder {
            color: $neutral_300;
        }
    }
}
