/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../../../Constant/font.scss" as *;
@use "../../../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactName_wrapper {
    margin-bottom: 3rem;
}

.contactName_name {
    font: $fs_r_12;
    color: $gray_1;
    line-height: 2rem;
    margin-bottom: 0.5rem;
}

.contactName_ipt {
    box-sizing: border-box;
}

.contactName_iptContent {
    box-sizing: border-box;

    input[class*="resetInput_input"] {
        font: $fs_r_14;
        color: $gray_2;
        line-height: 2.2rem;
    }
}

.contactName_iptContent__error {
    border-color: $error !important;
}
