/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.counter_outerContainer {
    position: relative;
    user-select: none;
}

.counter_container {
    font: $fs_r_14;
    display: inline-flex;
    border: 0.1rem solid $line_dark;
    border-radius: 0.4rem;
    box-sizing: border-box;
    justify-content: space-between;
}

.counter_container__disabled {
    border: 1px solid $line_light !important;
    > div {
        color: $line_light !important;
    }
}

.counter_container__noBorder {
    font: $fs_r_14;
    display: inline-flex;
    box-sizing: border-box;
    justify-content: space-between;
}

.counter_selectedItem {
    display: inline-block;
    margin-left: 1.2rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: $type;
}

.counter_openButton {
    display: inline-block;
    margin-right: 1.2rem;
    color: $line_light;
    transition: 0.1s linear;
}

.counter_actionButton {
    cursor: pointer;
    height: 50%;
    display: flex;
    align-items: center;
}

.counter_actionButton__disabled {
    color: $line_light !important;
    cursor: default;
}

.counter_actionButton:hover {
    color: $primary_variant;
}

/* <--------------------------- * SECTION1 * --------------------------- */
