/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import "../../../Constant/breakPoint.scss";
@import "../../../Constant/font.scss";
@import "../../../Constant/color.scss";
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin centerIcon {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    transition: all 0.5s ease;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */

.contacts_v2_wrap {
    display: inline-table;
}

.contacts_v2_openIcon {
    font-size: 1.5rem;
    color: $line_dark;
    transition: all 0.5s ease;
    user-select: none;
}

.contacts_v2_toggleBtn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    &:hover {
        .contacts_v2_openIcon {
            color: rgba($primary_variant, 0.5);
        }
    }
    span {
        cursor: pointer;
    }
}

.contacts_v2_toggle_wrap {
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    font-weight: 500;
    color: $neutral_600;
    letter-spacing: 0.01rem;

    .contacts_v2_toggle_item {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        color: $background;
        width: 2.4rem;
        height: 2.4rem;
        border-radius: 50%;
    }
    .contact_toggle_limit {
        background-color: $primary_100;
        color: $primary_600;
        line-height: 1.4rem;
        cursor: default;
    }
    .contacts_v2_toggle_text {
        color: $neutral_600;
        line-height: 2.1rem;
    }
    > span {
        margin-right: 0.8rem;
    }

    > div {
        margin-top: 0;
    }
}

.contacts_v2_toggleBtn__active.contacts_v2_toggleBtn {
    .contacts_v2_openIcon {
        color: $primary_variant;
    }
}

.contacts_v2_floatingWrap {
    width: 65rem;
    height: 41.5rem;
    background-color: $background;
    border-radius: 5px;
    box-shadow: $shadow_04dp;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-flow: row nowrap;
}

.contacts_v2_listContainer {
    width: 50%;
    height: 100%;
}

.contacts_v2_listTop {
    // height: 9.6rem;
    margin: 3.2rem 0 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contacts_v2_listTopName {
    position: relative;
    font: $fs_r_24;
    color: $type;
    line-height: 2.4rem;
}

.contacts_v2_listTopHr {
    width: 4rem;
    height: 0.4rem;
    background-color: $primary_variant;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 1rem);
    margin: 0 auto;
}

.contacts_v2_listAdd {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.4rem;
    width: 100%;
    height: 5.2rem;
    box-shadow: 0px -1px 5px -2px rgba(26, 26, 26, 0.12);
}

.contacts_v2_listAddBtn {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font: $fs_r_14;
    color: $primary_600;
    transition: all 0.5s ease;
}

.contacts_v2_listAddIcon {
    font-size: 1.4rem;
    color: $primary_600;
    margin-right: 0.65rem;
    transform: all 0.5s ease;
}

.contacts_v2_closeIcon {
    position: absolute;
    right: 2rem;
    top: 2rem;
    cursor: pointer;
    color: $line_light;
    transition: all 0.3s ease;
    &:hover {
        transform: scale(1.3);
    }
}

.contacts_v2_listAddBtn.contacts_v2_listAddBtn_gray {
    cursor: context-menu;
    color: $neutral_300;
    .contacts_v2_listAddIcon {
        color: $neutral_300;
    }
}

.contacts_v2_listContent {
    margin: 0;
    padding: 0;
}

.contacts_v2_contactItem {
    list-style: none;
    margin: 0;
    width: 100%;
    height: 6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    &:not(.contacts_v2_contactItem__disabled):hover {
        .contacts_v2_contactPersonal {
            opacity: 0;
            z-index: -1;
        }
        .contacts_v2_removeIcon {
            z-index: 1;
            opacity: 1;
        }
    }
}

.contacts_v2_contactIcon {
    width: 6rem;
    height: 100%;
    position: relative;
}

.contacts_v2_contactPersonal {
    width: 2rem;
    height: 2rem;
    background-color: $line_light;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    @include centerIcon();
    z-index: 1;
    border-radius: 50%;
}

.contacts_v2_contactPersonalIcon {
    font-size: 1.2rem;
    color: $background;
}

.contacts_v2_removeIcon {
    color: #eb5757;
    font-size: 2rem;
    z-index: -1;
    opacity: 0;
    @include centerIcon();
    cursor: pointer;
}

.contacts_v2_contactData {
    display: flex;
    width: calc(100% - 6rem);
    height: 100%;
    align-items: flex-start;
    justify-content: center;
    flex-flow: column nowrap;
}

.contacts_v2_contactName {
    width: 100%;
    font: $fs_r_14;
    color: $gray_1;
    line-height: 2.2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.contacts_v2_contactEmail {
    width: 100%;
    font: $fs_r_12;
    color: $line_dark;
    line-height: 2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.contacts_v2_contactItem__active {
    background-color: $bg;
}

// cn style
.contacts_v2_cn {
    .contacts_v2_toggle_text {
        @include c_h_7;
    }
    .contacts_v2_listAddBtn {
        @include c_body_3;
    }
}
