/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/** The demo mixin is ..........*/

@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * pagintion component start* --------------------------- */

.pagination_container {
    user-select: none;
    .pagination_content {
        > div {
            display: inline-block;
            background: transparent;
            border: 0.1rem solid $line_light;
            box-sizing: border-box;
            border-radius: 0.2rem;
            cursor: pointer;
            margin-right: 0.8rem;
            color: $type;
            width: 3.2rem;
            height: 3.2rem;
            text-align: center;
            line-height: 3.2rem;
            > button {
                border: none;
                background-color: inherit;
                outline: none;
            }
        }
        > span {
            margin-right: 0.8rem;
        }
    }
}

.electedPageNumber {
    background-color: $primary_variant;
    color: $primary_variant;
}

.pagination_button__active {
    > button {
        cursor: pointer;
    }
}

.pagination_button__inactive {
    cursor: default !important;
}

.pagination_button__active:hover {
    border-color: $primary_variant !important;
}

.pagination_button__active:hover button svg {
    color: $primary_variant;
}

/* <--------------------------- * pagintion component end * --------------------------- */
