/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/breakPoint.scss" as *;
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
@mixin absolute {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

@mixin PseudoElement {
    content: "";
    display: block;
    position: absolute;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.cutAvatar_container {
    width: 48rem;
    display: inline-flex;
    flex-direction: column;
}

.cutAvatar_content {
    display: flex;
}

.cutAvatar_clipWrap {
    width: 100%;

    .cutAvatar_upload {
        height: 31rem;
    }
}

.cutAvatar_clipWrap_cut {
    width: 30rem;
    .cutAvatar_upload {
        height: 100%;
    }
}

.cutAvatar_upload_main {
    position: relative;
    padding-top: 31rem;
}

.cutAvatar_upload_main_cut {
    padding-top: 100%;
}

.cutAvatar_upload {
    @include absolute;
}

.cutAvatar_upload_hide {
    display: none;
}

.cutAvatar_content_bg {
    @include absolute;
    overflow: hidden;
}

.cutAvatar_content_bg_cut {
    &::after {
        @include PseudoElement;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: url("../../../Assets/images/bg_mark.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}

.cutAvatar_content_bg_normal {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cutAvatar_content_bg_hide {
    display: none;
}

.cutAvatar_content_btns {
    display: flex;
    margin-top: 1.6rem;
    justify-content: space-between;
    align-items: center;

    & > span {
        // @include c_body_3;
        color: $neutral_300;
    }
    .cutAvatar_content_errmsg {
        color: $danger_600;
    }
}

.cutAvatar_content_reUpload {
    & > button {
        // @include c_body_3;
        border: none;
        background-color: transparent;
        color: $primary_600;
        cursor: pointer;
    }
}

.cutAvatar_content_operation {
    display: flex;
    font-size: 1.6rem;
    color: $neutral_300;
    & > span {
        margin-right: 1.6rem;
        cursor: pointer;
        &:hover {
            color: $primary_400;
        }
    }

    & > span:last-child {
        margin-right: 0;
    }
}

.cutAvatar_content_img {
    @include absolute;
    display: block;
    width: 100%;
    height: 100%;
}

.cutAvatar_grid {
    @include absolute;
    display: block;
    box-sizing: border-box;
}

.cutAvatar_grid {
    border: 0.1rem dashed $white;
    display: block;
}

.cutAvatar_grid_col {
    &::after,
    &::before {
        @include PseudoElement;
        top: 0;
        bottom: 0;
        border-left: 0.1rem dashed $white;
    }
    &::after {
        left: calc(100% / 3);
    }
    &::before {
        left: calc(100% / 3 * 2);
    }
}

.cutAvatar_grid_row {
    &::after,
    &::before {
        @include PseudoElement;
        left: 0;
        right: 0;
        border-top: 0.1rem dashed $white;
    }
    &::after {
        top: calc(100% / 3);
    }
    &::before {
        top: calc(100% / 3 * 2);
    }
}

.cutAvatar_preview {
    margin-left: 6rem;
}

.cutAvatar_preview_container {
    width: 12rem;
    height: 12rem;
    overflow: hidden;
    border-radius: 50%;
    background-size: cover;
}

.cutAvatar_preview_img {
    position: relative;
    width: 100%;
    height: 100%;
    user-select: none;
}

.cutAvatar_preview_img__placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: $primary_600;
    color: $white;
    font-size: 2rem;
    user-select: none;
}

.cutAvatar_preview_text {
    // @include c_body_3;
    display: inline-flex;
    margin-top: 1.2rem;
    width: 100%;
    justify-content: center;
    color: $neutral_300;
}

.cutAvatar_hr {
    margin: 3.2rem 0 2.4rem;
    width: 100%;
    height: 0.1rem;
    background-color: $neutral_200;
}

.cutAvatar_operation {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cutAvatar_operation_cancel {
    // @include c_body_3;
    border: none;
    background-color: transparent;
    color: $primary_600;
    cursor: pointer;
}

.cutAvatar_operation_confirm {
    // @include c_body_3;
    border: none;
    color: $white;
    padding: 0.6rem 1.6rem;
    border-radius: 0.6rem;
    background-color: $primary_600;
    margin-left: 1.8rem;
    cursor: pointer;
}

.cutAvatar_operation_confirm__disabled {
    background-color: $neutral_300;
    cursor: not-allowed;
}

// cn style
[class*="_cn"] {
    .cutAvatar_content_btns {
        & > span {
            @include c_body_3;
        }
    }
    .cutAvatar_content_reUpload {
        & > button {
            @include c_body_3;
        }
    }
    .cutAvatar_preview_text {
        @include c_body_3;
    }
    .cutAvatar_operation_cancel {
        @include c_body_3;
    }
    .cutAvatar_operation_confirm {
        @include c_body_3;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
