/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;


/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.dropDownInput_container {
    border: 0.1rem solid $line_light;
    border-radius: 0.4rem;
    height: 3rem;
    display: flex;
    align-items: center;
    user-select: none;
    position: relative;
    background: $background;
    > span {
        display: inline-flex;
        height: 2.2rem;
        border-right: 0.1rem solid rgba($color: $line_light, $alpha: 0.7);
        padding: 0 1rem;
        font-size: 1.5rem;
        color: $line_light;
        align-items: center;
        cursor: pointer;
        > span {
            display: inline-block;
            font: $fs_r_14;
            max-width: 10rem;
            text-overflow: ellipsis;
            white-space: nowrap;
            vertical-align: top;
            overflow: hidden;
            line-height: 2.2rem;
            color: $primary_variant;
            margin-right: 1rem;
        }
        &:focus {
            outline: none;
        }
    }
    > input {
        width: 100%;
        border: none;
        &:focus {
            outline: none;
        }
    }
}

.dropDownInput_dropDownContainer {
    background: $background;
    box-shadow: $shadow_04dp;
    padding: 1.5rem 1rem;
    font: $fs_r_14;
    color: $type;
    border-radius: 0.4rem;
    > div {
        margin-top: 1rem;
        > span {
            color: $line_light;
            margin-left: 0.8rem;
            font-size: 1.5rem;
            position: relative;
            > div {
                position: absolute;
                display: none;
                left: 50%;
                top: 2.5rem;
                z-index: 2;
                transform: translateX(-50%);
            }
            &:hover {
                > div {
                    display: block;
                }
            }
        }
        &:hover {
            color: $primary_variant;
            transition: 0.2s;
            cursor: pointer;
        }
    }
    > div:first-of-type {
        margin-top: 0rem;
    }
}
.dropDownInput_dropDownItem {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.dropDownInput_icon {
    font-size: 1.2rem;
}

/* <--------------------------- * SECTION1 * --------------------------- */
