/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.hideList_questionContainer {
    box-sizing: border-box;
    border-radius: 0.4rem;
    background: $background;
    user-select: none;
    box-shadow: $shadow_06dp;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    padding-right: 1.9rem;
    padding-left: 1.9rem;
}

.hideList_dotContainer {
    box-shadow: $shadow_01dp;
    border-radius: 0.5rem;
    background: $background;
    user-select: none;
}

.hideList_content__selected {
    height: 2rem;
    line-height: 2rem;
    color: $primary_variant;
}

.hideList_check {
    position: absolute;
    height: 3.4rem;
    line-height: 3.4rem;
    top: 0;
    right: 0;
}

.hideList_signOut {
    display: inline-block;
    margin-left: 2rem;
    color: $line_light;
}

.hideList_content {
    height: 2rem;
    line-height: 2rem;
}

.hideList_delete {
    height: 2rem;
    line-height: 2rem;
    color: $primary_variant;
}

.hideList_divider {
    height: 0.1rem;
    width: 100%;
    background: $not_activated;
}

.hideList_itemContainer {
    font: $fs_m_14;
    display: block;
    color: $type;
    padding-bottom: 0.7rem;
    padding-top: 0.7rem;
    position: relative;
}

.hideList_itemContainer:hover > .hideList_content {
    color: $primary_variant;
    transition: 0.1s linear;
}

.hideList_contentDelete {
    font: $fs_r_12;
    display: inline-block;
    height: 4.5rem;
    line-height: 4.5rem;
    color: rgba($error, 0.89);
    margin-left: 2.4rem;
}

.hideList_contentConvert {
    font: $fs_r_12;
    display: inline-block;
    height: 4.5rem;
    line-height: 4.5rem;
    color: rgba($type, 0.89);
    margin-left: 2.4rem;
    position: relative;
}

.hideList_itemContainer:hover > .hideList_contentConvert {
    color: $primary_variant;
    transition: 0.1s linear;
}

.hideList_contentIcon {
    display: inline-block;
    position: absolute;
    right: -5rem;
    font-size: 1.4rem;
    color: $type;
}

.hideList_contentAddition {
    position: absolute;
    top: 0;
    left: 13rem;
}

/* <--------------------------- * SECTION1 * --------------------------- */
