/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../Constant/breakPoint.scss" as *;
@use "../../Constant/font.scss" as *;
@use "../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

@mixin wrap {
    width: 120rem;
    margin: 0 auto;
    @media (max-width: 1440px) {
        width: calc(100% - 8rem);
    }
    @media (max-width: ($desktop )) {
        width: calc(100% - 6rem);
    }
    @media (max-width: $md) {
        width: calc(100% - 4.8rem);
    }
}

/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/** The demo mixin is ..........*/

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
.footer_wrapper {
    width: 100%;
    &Primary {
        background-color: $primary_variant;
    }
    &White {
        background-color: $background;
    }
}

.footer_link {
    @include wrap();
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    flex-flow: row nowrap;
}

.footer_linkRight {
    list-style: none;
    margin: 0;
    padding: 0;
    flex: 0 0 auto;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    justify-content: center;
}

.footer_linkRightItem {
    padding: 0;
    width: 100%;
    list-style: none;
    font: $fs_l_14;
    line-height: 2.2rem;
    margin: 0 0 1.5rem;
    &:last-of-type {
        margin-bottom: 0;
    }
}

.footer_hr {
    @include wrap();
    height: 0.5px;
    margin-bottom: 1.5rem;
}

.footer_linkLeft {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-flow: row nowrap;
    padding-top: 3rem;
    padding-bottom: 1.5rem;
}

.footer_linkGroup {
    margin-right: 7rem;
    &:last-of-type {
        margin-right: 0;
    }
}

.footer_linkTitle {
    font: $fs_m_16;
    line-height: 2.4rem;
}

.footer_linkList {
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer_linkItem {
    list-style: none;
    padding: 0;
    margin: 1.5rem 0 0;
    font: $fs_r_14;
    line-height: 2.2rem;
}

.footer_under {
    @include wrap();
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-flow: row nowrap;
    padding-bottom: 3rem;
}

.footer_copyright {
    font: $fs_l_14;
    line-height: 2.2rem;
    flex: 1 1 auto;
}

.footer_filing {
    a {
        text-decoration: none;
        color: $line_dark;
    }
    a:hover {
        color: $primary_500;
    }
}

.footer_platform {
    flex: 0 0 auto;
}

.footer_platformType {
    font-size: 2rem;
    margin-left: 0.5rem;
    &:first-of-type {
        margin-left: 0;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

.footer_wrapperPrimary {
    .footer_linkRightItem {
        color: $background;
    }

    .footer_linkTitle {
        color: $background;
    }

    .footer_linkItem {
        color: $gray_4;
    }

    .footer_hr {
        background-color: $background;
    }

    .footer_copyright {
        color: $background;
    }

    .footer_platformType {
        color: $background;
    }
}

.footer_wrapperWhite {
    .footer_linkRightItem {
        color: $line_dark;
    }

    .footer_linkTitle {
        color: $gray_1;
    }

    .footer_linkItem {
        color: $line_dark;
    }

    .footer_hr {
        background-color: $line_light;
    }

    .footer_copyright {
        color: $line_dark;
    }

    .footer_platformType {
        color: $primary_variant;
    }
}

/* <------------------------------------ **** 2 END **** ------------------------------------ */

@media (max-width: 1440px) {
    .footer_linkRightItem {
        font: $fs_l_14;
        line-height: 2.2rem;
        margin-bottom: 1rem;
    }

    .footer_linkGroup {
        margin-right: 6rem;
    }

    .footer_linkTitle {
        font: $fs_m_14;
        line-height: 2.2rem;
    }

    .footer_linkItem {
        font: $fs_r_14;
        line-height: 2.2rem;
        margin-top: 1rem;
    }

    .footer_copyright {
        font: $fs_l_14;
        line-height: 2.2rem;
    }

    .footer_platformType {
        font-size: 1.8rem;
    }
}

@media (max-width: ($desktop)) {
    .footer_linkRightItem {
        font: $fs_l_12;
        line-height: 2rem;
        margin-bottom: 0.7rem;
    }
}

@media (max-width: $md) {
    .footer_platformType {
        font-size: 1.6rem;
    }

    .footer_link {
        display: block;
    }

    .footer_linkLeft {
        display: block;
    }

    .footer_linkGroup {
        margin-bottom: 2.4rem;
        margin-right: 0;
        &:last-of-type {
            margin-bottom: 0;
        }
    }

    .footer_linkRight {
        @include wrap();
    }

    .footer_linkRightItem {
        font: $fs_l_14;
    }

    .footer_under {
        display: block;
    }

    .footer_copyright {
        line-height: 1.8rem;
        margin: 1.2rem 0;
    }

    .footer_wrapperPrimary {
        .footer_linkRightItem {
            color: $gray_4;
        }
    }
}
