/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@use "../../Constant/font.scss" as *;
@use "../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.divider_container {
    font: $fs_r_14;
    color: $line_light;
    display: flex;
    align-items: center;
    > span {
        margin: 0;
        padding: 0 1.6rem;
    }
    > div {
        flex: 1;
        height: 0.1rem;
        background: $line_light;
    }
}

.divider_textPosition {
    flex: none !important;
    flex-basis: 2rem !important;
}

