/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import "../../../../../Constant/breakPoint.scss";
@import "../../../../../Constant/font.scss";
@import "../../../../../Constant/color.scss";
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.contactName_wrapper {
    margin-bottom: 3rem;
}

.contactName_name {
    font: $fs_r_12;
    color: $gray_1;
    line-height: 2rem;
    margin-bottom: 0.5rem;
}

.contactName_ipt {
    position: relative;
    top: 0;
    left: 0;
    box-sizing: border-box;
}

.contactName_iptContent {
    box-sizing: border-box;
    background-color: $background;
    input[class*="resetInput_input"] {
        font: $fs_r_14;
        color: $gray_2;
        line-height: 100% !important;
    }
}

.contactName_ipt_errormsg {
    position: absolute;
    bottom: -2rem;
    left: 0;
    color: $danger_600;
}

.contactName_iptContent__error {
    border-color: $danger_600 !important;
}

// cn style
.contactName_cn {
    .contactName_name {
        @include c_body_3;
    }

    input[class*="resetInput_input"] {
        @include c_body_3;

        &::placeholder {
            color: $neutral_300;
        }
    }
}
