/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/breakPoint.scss" as *;
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

.fullImage_wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.fullImage_bg {
    background-color: rgba($neutral_700, 0.5);
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
}

.fullImage_image {
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    transition: transform 0.2s linear;
}

.fullImage_loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.fullImage_downloadContainer {
    background-color: $primary_600;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 6.4rem;
    padding: 0 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-flow: row nowrap;
    box-sizing: border-box;
}

.fullImage_fileName {
    font: $fs_m_16;
    color: $background;
}

.fullImage_downloadBtn {
    cursor: pointer;
    color: $background;
    font: $fs_r_14;
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    flex-flow: row nowrap;
    text-decoration: none;
}

.fullImage_downloadIcon {
    font-size: 1.6rem;
    color: $background;
    margin-right: 1rem;
}

.fullImage_closeBtn {
    position: absolute;
    top: 8.8rem;
    right: 2.4rem;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    &:hover {
        .fullImage_closeIcon {
            transform: scale(1.2);
        }
    }
}

.fullImage_closeIcon {
    color: $background;
    font-size: 1.8rem;
    transition: all 0.2s linear;
}
